body{
    margin: 0;
    background-color: blanchedalmond;
}
.todo-container{
    background-color: aliceblue;
    width:500px;
    position:absolute;
    margin-left: 20px;
    margin-top: 30px;
}
ul{
 
    text-align: left;
    
    padding: 0;
}

ul li{
    display: flex;
    list-style: none;
    padding:15px;
    border-radius: 5px;
    margin-bottom: 10px;
}
ul li i{
    text-align: right;
    flex:1;
    cursor: pointer;
}